@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
/* MINIMAL RESET  */
/* ========================== */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* FONTS */
/* ================= */

@font-face {
  font-family: "bpg";
  src: url(/redberry/static/media/bpg_nino_mtavruli_bold.43eacc71.ttf);
}

@font-face {
  font-family: bpgSquare;
  src: url(/redberry/static/media/bpg_extrasquare_2009.36a858ca.ttf);
}

/* GENERAL */
/* ============== */

body {
  background-color: #eaeaea;
}

/* FIRST PAGE */
/* ================ */

.logoParent {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #eaeaea;
}

.mainLogo {
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  max-height: 100%;
  margin-top: 6rem;

  will-change: auto;

  animation: scale 0.4s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
}

@keyframes scale {
  from {
    width: 100vw;
    height: 100vh;
  }
  to {
    width: 100px;
    height: 100px;
    margin-top: 1rem;
  }
}

.slide-bottom {
  margin-top: 2rem;
  /* overflow: hidden; */
  max-width: 500px;
}

.slide-bottom button {
  border: none;
  cursor: pointer;
  background-color: #eaeaea;

  animation: 0.4s slide-bottom 1s forwards;
  will-change: scroll-position;
  opacity: 0;
  transform: translateY(-40%);
  max-width: 200px;
  font-family: bpg;
  font-size: 2rem;
  color: #232323;
}

@keyframes slide-bottom {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-bottom button:hover {
  text-decoration: underline;
}

/* IDENTIFICATION PAGE  */
/* ================================= */

.identificationParent {
  padding: 6rem 10rem;
  background-color: #eaeaea;
}

.identification {
  background-color: #eaeaea;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.identificationForm input {
  display: block;
}

.identification input:not(.identificationSubmit) {
  width: 513px;
  height: 50px;
  font-size: 1.4rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  background-color: #eaeaea;
  border: 0.8px solid #c4c4c4;
  font-family: bpgSquare;
  font-weight: 400;
  opacity: 0.8;
  color: #232323;
  padding: 1.8rem;
}

.inputParentDiv {
  margin-bottom: 2.5rem;
}

.identification label {
  font-size: 1.3rem;
  font-weight: 500;
  color: #232323;
  font-family: bpgSquare;
  font-weight: 600;
}

.identificationSubmit {
  width: 40px;
  cursor: pointer;
  border: none;
  background-color: #eaeaea;
  margin-left: calc(50% - 20px);
  margin-top: 3rem;
  margin-bottom: 2rem;
}

.errorNotif {
  color: #f15524;
  font-family: bpgSquare, sans-serif;
}

@media (max-width: 1335px) {
  .identification input:not(.identificationSubmit) {
    width: 100%;
  }
}

@media (max-width: 1069px) {
  .identification {
    flex-direction: column-reverse;
    align-items: center;
  }
}

@media (max-width: 696px) {
  .identification .peopleImg {
    max-width: 260px;
    margin-top: -2rem;
  }

  .identificationParent {
    padding: 2rem;
  }

  #identificationForm {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .identification input:not(.identificationSubmit) {
    padding: 0.5rem;
    margin-bottom: 1.2rem;
    margin-top: 0.6rem;
  }
}

.arrowBtnRight {
  background: url(/redberry/static/media/right-arrow.f03ab693.svg) no-repeat left center;
  padding-left: 20px;
}

.arrowBtnLeft {
  background: url(/redberry/static/media/left-arrow.b10e190b.svg) no-repeat left center;
  padding-left: 20px;
}

.headerNav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-family: "Raleway", sans-serif;
  font-size: 1.4rem;
  border-bottom: 1.8px solid #232323;
  padding-bottom: 5px;
  margin-bottom: 4rem;
}

.headerNav span:first-child {
  color: #fe3b1f;
  letter-spacing: -1.1px;
}

.requiredText {
  color: #626262;
  border-top: 1px solid black;
  margin-top: 2rem;
  padding-top: 1rem;
  max-width: 300px;
}

.peopleImg {
  max-width: 100%;
  display: block;
  height: auto;
  margin-top: 1rem;
}

@media (max-width: 1570px) {
  .peopleImg {
    max-width: 500px;
  }
}

.inputParentDiv {
  width: 390px;
}

@media (max-width: 500px) {
  .inputParentDiv {
    max-width: 300px;
  }
}

@media (max-width: 340px) {
  .inputParentDiv {
    max-width: 230px;
  }
}

/* COVID QUESTIONS  */
/* ================================  */

.covidQuestions {
  font-family: bpgSquare;
  padding: 6rem 10rem;
}

.covidIdentification {
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}

.page2Image {
  margin-top: -4rem;
  max-width: 900px;
}

@media (max-width: 890px) {
  .identification.covidIdentification {
    flex-direction: column-reverse;
    align-items: center;
  }
  .identification.covidIdentification img {
    margin-top: -2rem;
    margin-bottom: 2rem;
  }
}

@media (max-width: 1570px) {
  .page2Image {
    max-width: 400px;
    margin-top: -3rem;
    margin-right: -4rem;
  }
}

.buttonsParent {
  margin-left: calc(50% - 60px);
  margin-bottom: 1rem;
}

.buttonsParent a {
  margin-bottom: 2rem;
}

.covidQuestions .identificationSubmit {
  margin: 0;
}

@media (max-width: 696px) {
  .page2Image {
    max-width: 270px;
    margin-left: -3rem;
    margin-top: 0;
  }
  .covidQuestions {
    padding: 3rem;
  }

  .buttonsParent {
    margin-left: calc(50% - 30px);
    margin-top: 2rem;
  }
}

@media (max-width: 500px) {
  .covidQuestions {
    padding: 2rem;
  }
}

@media (max-width: 1000px) {
  .buttonsParent {
    margin-top: 2rem;
  }
}

.radioItem {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.radioItem.radioItem input {
  width: 23px;
  margin-right: 1rem;
  height: 23px;
  margin-left: 0.7rem;
}

.radioItem.radioItem label {
  font-size: 1rem;
}

#covidForm p,
#vaccinationForm p {
  margin-top: 1.5rem;
  font-size: 1.2rem;
}

#covidForm input[type="date"],
#covidForm input[type="text"] {
  height: auto;
  padding: 1rem;
}

#covidForm > div:last-child input:not([type="radio"]) {
  margin-left: 1rem;
}

/* page3 */
/* ================= */

.doctorImg {
  margin-left: -3rem;
  margin-top: -6rem;
}

@media (max-width: 1600px) {
  .doctorImg {
    max-width: 600px;
  }
}

@media (max-width: 1200px) {
  .doctorImg {
    max-width: 300px;
    margin-top: -3rem;
  }

  #vaccinationForm label {
    max-width: 70%;
    margin-top: 0.5rem;
    margin-right: 1rem;
  }
}

.toLeftH5 {
  max-width: 400px;
  margin-top: 1rem;
}

@media (min-width: 1300px) {
  .toLeftH5 {
    margin-left: 4rem;
  }
}

#vaccinationForm a {
  color: #1289ae;
}

/* ADVICES */
/* ======================== */

.advicesDiv {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.advicesDiv > * {
  max-width: 50%;
}

.advicesDiv img {
  align-self: flex-start;
  margin-top: 1rem;
}

#advicesForm {
  display: flex;
  flex-direction: column;
}

#advicesForm button {
  align-self: flex-end;
  background-color: #208298;
  width: 180px;
  border-radius: 42px;
  color: white;
  font-size: 1.2rem;
  font-family: bpgSquare;
  height: 56px;
  border: none;
  margin-top: 3rem;
  cursor: pointer;
}

@media (max-width: 1300px) {
  .advicesDiv {
    flex-direction: column;
    align-items: center;
  }

  .advicesDiv > * {
    max-width: 100%;
  }

  .advicesDiv img {
    align-self: center;
    margin-top: 1rem;
  }

  #advicesForm button {
    align-self: center !important;
  }
}

.advicesFormParent > *,
.advicesFormParent form > * {
  margin-bottom: 2rem;
}

.advicesFormParent form > div > div {
  margin: 0.5rem 0;
}

.advicesFormParent textarea {
  width: 622px;
  height: 188px;
  margin-top: 1rem;
  padding: 1rem;
  font-family: bpgSquare;
  font-size: 1.2rem;
  color: #232323;
}

#advicesForm .parentOfLastTextarea {
  max-width: 622px;
}

@media (max-width: 1300px) {
  .advicesFormParent textarea {
    max-width: 400px;
  }
  #advicesParent {
    padding: 4rem;
  }
}

@media (max-width: 500px) {
  .advicesFormParent {
    max-width: 360px;
    padding: 0 2rem;
  }
  .advicesFormParent textarea {
    max-width: 250px;
  }
  .advicesFormParent p {
    margin-top: 1rem;
  }
}

#advicesForm .parentOfLastTextarea {
  display: flex;
  flex-direction: column;
}

#advicesForm .parentOfLastTextarea button {
  align-self: flex-end;
}

.disabled {
  opacity: 0.4;
  cursor: default !important;
}

/* FINAL  */
/* ============================ */

.final {
  background-color: #232323;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #eaeaea;
}

.final > div {
  text-align: center;
  padding: 1rem;
  animation: show 5s;
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.final h1 {
  font-size: 3rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.final > div > * {
  margin-bottom: 1rem;
}

.final p {
  font-family: bpgSquare;
}

.final a {
  color: #1289ae;
}

/* RESULT  */
/* =============== */

.result {
  padding: 3rem;
  font-family: bpgSquare;
}

.result p:first-child {
  text-decoration: underline;
  margin-bottom: 0.5rem;
  color: #1289ae;
  font-size: 1.2rem;
}

.result p:last-child {
  margin-bottom: 1rem;
  color: #232323;
  font-size: 1.1rem;
}

